<template>
  <div style="padding-top: 40px">
    <div style="display: flex; justify-content: center; align-items: center">
      <div style="display: flex; align-items: center">
        <h2 style="margin-right: 10px">Material - RETURN</h2>
        <button @click="goBack">back</button>
      </div>
    </div>
    <form @submit.prevent="returnBook">
      <label style="font-size: 20px">
        Book ID:
        <input
          style="width: 180px; height: 30px"
          v-model="bookid"
          type="text"
          required
        />
      </label>

      <label style="font-size: 20px">
        user ID:
        <input
          style="width: 180px; height: 30px"
          v-model="userid"
          type="text"
          required
        />
      </label>

      <button
        style="margin-left: 10px; width: 180px; height: 30px; font-size: 20px"
        type="submit"
      >
        return Book
      </button>
    </form>
  </div>
</template>

<script>
import "whatwg-fetch";
import axios from "axios";

export default {
  name: "returnBook",
  data() {
    return {
      bookid: "",
      userid: "",
    };
  },
  methods: {
    returnBook() {
      axios
        .put("/staff/material/return/" + this.bookid + "/" + this.userid)
        .then((response) => {
          // 成功处理响应
          console.log('response',response);
          if (response.data>0) {
            alert("Book returnedsuccessfully!");
            // 重置表单
            (this.bookid = ""), (this.userid = "");
          } else {
            alert("Failed to returne book.");
          }
        })
        .catch((error) => {
          // 处理响应出错
          console.log(error);
          alert("Failed to return book.");
        });
    },

    goBack() {
      window.location.href = "http://localhost:8080/staff.html";
    },
  },
};
</script>

<style>
input[type="text"] {
  font-size: 16px;
}
</style>