<template>
  <div style="padding-top: 40px">
    <div style="display: flex; justify-content: center; align-items: center">
      <div style="display: flex; align-items: center">
        <h2 style="margin-right: 10px">Material - DELETE</h2>
        <button @click="goBack">back</button>
      </div>
    </div>

    <form @submit.prevent="deleteBook">
      <label style="font-size: 20px">
        Book ID:
        <input
          style="width: 180px; height: 30px"
          v-model="bookid"
          type="text"
          required
        />
      </label>

      <button
        style="margin-left: 10px; width: 180px; height: 30px; font-size: 20px"
        type="submit"
      >
        delete Book
      </button>
    </form>
  </div>
</template>
  
  <script>
import "whatwg-fetch";

export default {
  name: "deleteBook",
  data() {
    return {
      bookid: "",
      userid: "",
    };
  },
  methods: {
   
    /*
    async deleteBook() {
      console.log("deleteBook,", "bookid:", this.bookid);
      const url = `/staff/material/delete/${this.bookid}`;
      const response = await fetch(url, { method: "DELETE" });
      console.log(response);
      if (response.ok) {
        alert("Book deleted successfully!");
        // 重置表单
        (this.bookid = ""), (this.userid = "");
      } else {
        alert("Failed to delete book.");
      }
    },
*/
    async deleteBook() {
      console.log("deleteBook,", "bookid:", this.bookid);
      const url = `/staff/material/delete/${this.bookid}`;
      const response = await fetch(url, { method: "DELETE" });
      const responseBody = await response.json(); // 从响应中获取 JSON 格式的响应体

      if (response.ok && responseBody.success) {
        alert("Book deleted successfully!");
        // 重置表单
        (this.bookid = ""), (this.userid = "");
      } else {
        alert("Failed to delete book.");
      }
    },

    goBack() {
      window.location.href = "http://localhost:8080/staff.html";
    },
  },
};
</script>

<style>
input[type="text"] {
  font-size: 16px;
}
</style>