import Vue from 'vue'
import App from './App.vue'
// import router from './router'
import store from './store'
import VueRouter from 'vue-router';

Vue.use(VueRouter);
import axios from 'axios'

Vue.prototype.$axios = axios

import returnBook from './returnBook.vue';
import deleteBook from './deleteBook.vue';
Vue.config.productionTip = false

const routes = [
  { path: '/staff/deleteMaterial', component:deleteBook },
  { path: '/staff/returnMaterial', component:returnBook }
];

const router = new VueRouter({
  routes,
  mode: 'history' // 设置路由模式为 history 模式
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
